body {
  max-width: 100%;
  margin: 0 auto;
  font-family: 'Manrope', -apple-system, BlinkMacSystemFont, 'Inter', 'Segoe UI', 'Roboto',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/src/assets/background.jpg);
  background-size: cover;
  background-repeat: no-repeat;

}

@media screen and (max-width: 768px) {
  body{
max-width: 768px;
 }
}
body::-webkit-scrollbar {
  width: 2px;
}

body::-webkit-scrollbar-track {
  background: gray;
  border-radius: 12px;
}

body::-webkit-scrollbar-thumb {
  background: #ffffff;
  border-radius: 12px;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
}

.icon-placeholder {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  flex: 40px 0;
  margin-right: 20px;
}

.text-placeholder {
  border-radius: 5px;
  width: 200px;
  height: 20px;
  flex: 1;
}

.refresh {
  padding: 10px;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  width: 20px;
  height: 20px;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
 
* {
  scroll-behavior: smooth;
}